@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Roboto',
        sans-serif;

}

h1,
h2,
h3,
h4,
h5,
h6,
p {
    padding: 0;
    margin: 0;
}

body {
    width: 100vw;
    height: 100vh;
    background-color: #1d1d1d;
    overflow-x: hidden;
}

.box {}

.ProfileCard__container {
    background: #ededed;
    border-radius: 8px;
    width: 300px;
    padding: 1rem 1.5rem;
    margin: 5rem 1rem 1rem 1rem;
    display: flex;
    position: relative;
    align-items: center;
    justify-content: center;
    z-index: 1;
    /* transform: scale(0.8); */
    transition: 0.45s ease;
}

.ProfileCard__container:hover {
    transform: scale(1.1);

}

.ProfileCard__container::before {
    content: "";
    border-radius: 8px;

    position: absolute;
    width: 100%;
    height: 20%;
    top: 0;
    background: linear-gradient(#fdc830, #f37335);
}

.ProfileCard__container .ProfileCard__image {
    width: 120px;
    height: 120px;
    border: 12px solid #1d1d1d;
    position: absolute;
    border-radius: 60px;
    top: 0;
    left: 50%;
    transform: translate(-50%, -50%);
    overflow: hidden;
}

.ProfileCard__container:hover .ProfileCard__image img {
    transform: scale(1.2);
}

.ProfileCard__container .ProfileCard__image img {
    width: 100%;
    transition: 0.45s ease;

    border-radius: 60px;
}

.ProfileCard__container .ProfileCard__data {
    margin-top: 25%;
    display: flex;
    flex-direction: column;
    align-items: center;
    line-height: 30px;
    position: relative;
    z-index: 9;
}



.ProfileCard__container .ProfileCard__data .link {
    background: linear-gradient(#fdc830, #f37335);
    padding: 0.2rem 0.8rem;
    color: white;
    text-decoration: none;
    border-radius: 50px;
    margin: 0.8rem;
}

@media only screen and (max-width: 600px) {
    .ProfileCard__container {
        width: 250px;
        padding: 1rem 0.5rem;
        margin: 5rem 1rem 1rem 1rem;

    }
}
.inputName__search-container {
  height: 40px;
  width: 100vw;
  position: relative;
  left: 0%;
  margin: 0.8rem 0rem;
}

.inputName__search-container .box {
  width: 50%;
  height: 100%;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: end;
  align-items: center;
}

.inputName__search-container .box::before {
  content: "";
  position: absolute;
  width: 0%;
  height: 5px;
  background: linear-gradient(#fdc830, #f37335);
  bottom: -5px;
  transition: 0.4s cubic-bezier(0.19, 1, 0.22, 1);
}

.inputName__search-container .box:hover::before {
  width: 100%;
}

.inputName__search-container .box input {
  width: 100%;
  height: 100%;
  padding: 0 60px 0 10px;
  border: none;
  color: linear-gradient(#fdc830, #f37335);
  outline: none;
}

.inputName__search-container .box .inputName__search {
  position: absolute;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  right: 10px;
  background: linear-gradient(#fdc830, #f37335);
  color: #ededed;
}

.profileBox {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 1rem auto;
  flex-wrap: wrap;
  width: 100vw;
}

.container {
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin: auto;
  width: 70%;
}

.submitBtn {
  background: linear-gradient(#fdc830, #f37335);
  padding: 0.2rem 0.8rem;
  color: white;
  text-decoration: none;
  border-radius: 50px;
  outline: none;
  border: none;
  transition: 0.4s ease-in-out;
}

.submitBtn:hover {
  box-shadow: 0px 0px 20px 5px #ff760082;
}


@media only screen and (max-width: 768px) {
  .inputName__search-container .box {
    width: 80%;
  }

  .submitBtn {
    background: linear-gradient(#fdc830, #f37335);
    padding: 0.1rem 0.4rem;
    color: white;
    text-decoration: none;
    border-radius: 50px;
    outline: none;
    border: none;
    font-size: 10px;
    transition: 0.4s ease-in-out;
  }

  .container {
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin: auto;
    width: 100%;
    flex-wrap: wrap;
  }

  .profileBox {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0.4rem auto;
    flex-wrap: wrap;
    width: 100vw;
  }
}
